html {
  background-color: LightYellow;
  height: 100vh;
}

body {
  max-width: 1000px;
  margin: auto;
  padding: 0;
  background-color: White;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mainHeading {
  margin-top: 0;
}

/* body {
  background-color: white;
  margin: auto;
  padding: 0 20px;
  max-width: 1000px;
} */

.content {
  padding: 20px;
}
.App-info {
  margin-top: calc(10px + 2vmin);
  font-size: smaller;
}

input, select, option {
  box-sizing: border-box;  
}

input, select {
  height: 22px;
}
.SiteInfoForm > div {
  margin: 0 10px 20px 0;
  width: 220px;
}

.SiteInfoForm > div * {
  width: 100%
}

.SiteInfoForm {
  font-size: smaller;
  display: flex;
  flex-flow: row wrap;
  justify-content: left;
}
.ResourceGrid {
  display: flex;
  flex-flow: column nowrap;
}

.Resource {
  display: flex;
  flex-flow: row wrap;
  margin-bottom: 5px;
}

.Resource-Label {
  flex-basis: 310px;
}

.Resource-Input {
  flex-grow: 1;
}

.Resource-CopyLink {
  margin: auto 0 auto 3px;
  font-size: smaller;
}

